.resume-card {
  margin: 30px 28px;
  width: 50%;
  margin: auto;
  background-color: #ffffff;
  font-size: 16px;
  border-radius: 15px;
}
.table-left-space {
  margin-top: 10vh;
}
.px-4 {
  text-align: center;
  margin-bottom: 35px;
}

.resume-card .project {
  width: 245px;
  font-size: 16px;
  border-radius: 15px;
  border: 1.5px #00000080 solid;
  padding: 3px 4px;
}

.resume-card .res-card1 .react-select {
  border: 1px #000 solid;
  font-size: large;
}
.react-select div {
  border: none;
}

.res-inform {
  width: 100%;
  height: 100%;
  background-color: #f1f3f6;
  border-radius: 5px;
  font-family: Poppins;
  box-shadow: none;
  padding: 10px 25px;
}

.res-head {
  font-weight: bold;
  color: #363636;
  font-size: 16px;
  display: flex;
  justify-content: center;
  margin-top: 20px;
  margin-bottom: 10px;
}
.res-details-wrap1 {
  margin-right: 5px;
}

.res-btn:hover {
  color: black;
}

.res-wrap {
  display: flex;
  font-family: Poppins;
  margin-bottom: 0;
}

.res-finalWrap {
  display: flex;
}

.res-finalWrap .confirm {
  border-radius: 5px;
  float: right;
  margin-bottom: 15px;
  margin-top: -65px;
}

.res-tableWrap {
  width: 100%;
  margin-top: 25px;
}

.res-tableWrap .rt-thead .rt-tr {
  height: 59px;
  align-items: center;
}

.res-details-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.res-Csvbtn {
  width: 145px !important;
  align-self: center !important;
  background-color: #f1f3f6 !important;
  color: black !important;
  min-width: 130px !important;
  font-weight: bold;
  border-left: rgb(185, 179, 179);
  border: 1px solid rgb(185, 179, 179);
}
.MuiOutlinedInput-inputMarginDense {
  padding-top: 5px;
  padding-bottom: 5px;
}

.res-iconBtn {
  width: 20px !important;
  height: 20px !important;
  background-color: #ffffff !important;
  color: black !important;
  margin: 5px;
}
.res-delete-icon {
  color: #ff5353;
  margin-top: -8px;
}
.res-email,
.res-name {
  margin-right: 5px;
}
.rs-search-bar {
  margin-bottom: 10px;
  justify-content: center;
  height: 60px;
  width: 99%;
}
.search-icon {
  margin-top: 35px;
  width: 90px;
  height: 60px;
  background-color: #e1e5eb;
  padding: 17px 30px;
  border-radius: 5px;
}
.search-icon img {
  height: 28px;
}
.MuiInputBase-root {
  background-color: #e1e5eb;
}

.rs-search-div {
  display: flex;

  width: 100%;
  margin-bottom: 0px;
  border: none;
}

.rs-search-div .flex {
  margin-top: 20px;
  width: 100%;
}

.res-card1 {
  width: 100%;
}

.errorMsg {
  color: red;
  font-size: 14px;
  margin-left: 5px;
  margin-top: 5px;
  margin-bottom: 5px;
}

.res-wrap .rs-search-master {
  border-radius: 10px;
  justify-self: center;
  align-self: center;
  height: 42px;
  /* border: none; */
  box-shadow: none;
  /* outline: none; */
  margin-right: 0px;
}
.res-status {
  display: block;
  margin: 0px 10px;
}
.res-verify-icon,
.res-upload-icon,
.res-upload-done-icon {
  margin: auto;
  margin-bottom: 15px;
}

.res-upload-text {
  font-size: 18px;
  font-weight: 500;
}
.res-wrap .red-border {
  border-radius: 10px;
  justify-self: center;
  align-self: center;
  height: 37px;
  border: 1px #ff5d5d solid;
  box-shadow: none;
  margin-right: 0px;
  color: #ff5d5d;
}
.res-select {
  position: absolute;
  left: 42%;
  top: 60%;
}
.res-file-reader {
  display: absolute;
}

.res-details-wrap .MuiInputBase-root {
  border-radius: 10px !important;
  font-size: 16px !important;
  background-color: white !important;
  padding: 3px 3px;
  width: 250px !important;
  /* height: 40px; */
  text-align: left;
}
.resume-card .res-card1 .red-border1 {
  border: 1px #ff5d5d solid;
  font-size: large;
}
.resume-card .res-card1 .fcolor {
  color: #ff5d5d;
}

.res-details-wrap .makeStyles-root-1 {
  width: 30%;
}

.res-tableWrap .rt-td {
  padding: 15px;
}

.resume-card .MuiCardContent-root {
  padding-bottom: 10px;
  margin-top: 5vh;
}

.button-div {
  display: flex;
}

.res-or {
  align-self: center;
  margin: 20px;
  font-family: Poppins;
  font-size: 20px;
  font-weight: bold;
}

.res-or-container {
  display: flex;
}
.MuiInputBase-root {
  padding: 0;
  border: none;
  height: 100%;
}
.pagination-bottom {
  justify-content: start;
}
.pagination > li > a {
  background-color: #ddd;
}
.pagination > li:first-child > a,
.pagination > li:first-child > span,
.pagination > li:last-child > a,
.pagination > li:last-child > span {
  background-color: transparent;
}
